.menuItem{
    height: 200px;
    margin: 15px;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    cursor: pointer;
    opacity: 0.9;
}
.menuItem .menuInfo{
    width: 100%;
    height: 200px;
    color: white;
    transition: background-color 0.3s;
}
.menuItem .menuInfo:hover{
    background-color: #fdbb2c90;
}
.menuItem .menuInfo p{
    font-size: 18px;
    font-weight: 400;
    padding: 3% 3% 0 3%;
}
.menuItem .menuInfo h3{
    font-size: 30px;
    font-weight: 600;
    padding: 0 3%;
}
.menuItem .menuImg{
    height: 200px;
    width: 100%;
}

/* scroll-animation */
.scroll{
    transition: all 1s ease;
    transform: translate(-400%,0);
}
.scroll.show{
    transform: none;
}

.Link{
    text-decoration: none;
}