.data{
    cursor: pointer;
}
.data:hover{
    background: rgb(246, 246, 246);
    border-left: 3px solid rgb(233, 185, 83);
}
tr.data td.title{
    color: black;
}
.on p{
    background: tomato;
    padding: 10px 5px;
    color: black;
}
.on p::after{
    content: "";
}
.off p{
    background: rgb(233, 211, 83);
    padding: 10px;
    color: black;
}
.off p::after{
    content: "";
}